import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import s from "./PopupBuy.module.css";
import { TextField } from "@material-ui/core";
import cn from "classnames";
import * as yup from "yup";
import { nameValidation, phoneValidation } from "../../../common/validations";
import { MaskPhone } from "../../shared/Mask";
import React, { useState } from "react";

const SignupSchema = yup.object().shape({
  ...nameValidation,
  ...phoneValidation,
});

export const PopupBuy = () => {
  const [send, setSend] = useState(false);
  const {
    register,
    handleSubmit,
    reset,
    control,
    formState: { errors },
  } = useForm({
    defaultValues: { phone: "" },
    resolver: yupResolver(SignupSchema),
    mode: "onTouched",
  });

  const onSubmit = (values) => {
    console.log(values);
    reset();
    setSend(true);
  };

  return (
    <div className={"popupWrap"}>
      <div className={cn("popupTitle", s.title)}> </div>
      <form onSubmit={handleSubmit(onSubmit)} noValidate>
        <div className={s.phone}>
          <TextField
            required
            type={"text"}
            label={""}
            error={!!errors.name}
            helperText={errors.name?.message}
            {...register("name")}
          />
        </div>
        <div>
          <MaskPhone
            required
            name="phone"
            control={control}
            errors={errors}
            label={""}
            placeholder={"+7 (995) 599 31 20"}
          />
        </div>

        <div className={s.subtext}>
              1  ( 9:00  21:00  
          )
        </div>
        <div className={cn("popupWrapButton", { popupSend: send })}>
          {send && (
            <div className={"popupSendText"}>  </div>
          )}
          <button className={cn("button", "popupButton")}></button>
        </div>
      </form>
    </div>
  );
};
